.menu-list {
    width: 224px;

    .menu-item {
        margin-left: 0;
    }
}
.menu-wrapper {
    padding-bottom: 8px;
}
.menu-body {
    overflow-y: auto;
}
.menu-item {
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    line-height: 1.8rem;
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }

    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }
}

// Header

.header {
    position: relative;
    flex-shrink: 0;
    height: 52px;
    margin-top: -8px;
}

.back-btn {
    width: 60px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
    font-size: 1.8rem;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
